// Use declare class to describe a class or class-like object. Classes can have properties and methods as well as a constructor.
export class UserDisplayModel {
    constructor() {
        this.id = 0;
        this.first_name = '';
        this.last_name = '';
        this.user_type = 0;
    }
}
export class UserModel extends UserDisplayModel {
    constructor() {
        super(...arguments);
        this.username = '';
        this.password = '';
        this.phone_number = '';
        this.classroom = 0;
        this.courseGroup = 0;
        this.joined_date = '';
    }
}
